.Background {
  min-height: 100vh;
  width: 100%;
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
  overflow: hidden;
  background-image: -webkit-gradient(linear, left top, left bottom, from(#133a5f), to(#63bd72));
  background-image: -webkit-linear-gradient(#133a5f, #63bd72);
  background-image: -o-linear-gradient(#133a5f, #63bd72);
  background-image: linear-gradient(#133a5f, #63bd72);
}


