.Image {
  display: block;
  width: 90%;
  height: 20rem;
}

.Image__container {
  width: 100%;
  height: 100%;
  background-size: cover;
  background-position: center;
}

@media (min-width: 550px) {
  .Image { width: 50%; }
}

@media (min-width: 768px) {
  .Image { width: 25%; }
}


