.SearchIcon {
  width: 3rem;
  border: 0;
  background: none;
  cursor: pointer;
  -webkit-transition: all .3s;
  -o-transition: all .3s;
  transition: all .3s;
}

.SearchIcon:focus {
  outline: none;
}

.SearchIcon:active {
  -webkit-transform: scale(.9);
      -ms-transform: scale(.9);
          transform: scale(.9);
}

.SearchIcon:active .SearchIcon__icon__top {
  border: 3px solid #505050;
} 

.SearchIcon:active .SearchIcon__icon__bottom {
  background-color: #505050;
} 

.SearchIcon__icon {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  width: 3rem;
  height: 3rem;
  -webkit-transform: rotate(45deg);
      -ms-transform: rotate(45deg);
          transform: rotate(45deg);
}

.SearchIcon__icon__top {
  width: 40%;
  height: 40%;
  border-radius: 100%;
  border: 3px solid #777;
  -webkit-transition: all .2s;
  -o-transition: all .2s;
  transition: all .2s;
}

.SearchIcon__icon__bottom {
  width: 5%;
  height: 20%;
  border-radius: 30%;
  background-color: #777;
  -webkit-transition: all .2s;
  -o-transition: all .2s;
  transition: all .2s;
}


