.App {
  -webkit-transform: translateY(35vh);
      -ms-transform: translateY(35vh);
          transform: translateY(35vh);
  -webkit-transition: all 1s;
  -o-transition: all 1s;
  transition: all 1s;
}

.App__header {
  padding: 1.5rem 0;
  text-align: center;
  color: #d1d1d1;
  text-transform: uppercase;
}

.App__header__main {
  font-weight: 700;
  font-size: 3rem;
}

.App__header__sub {
  font-size: 1.25rem;
  font-weight: inherit;
  letter-spacing: 1rem;
}

.App__searchFail {
  color: #d1d1d1;
  text-align: center;
  margin-top: 3rem;
  font-size: 1.3rem;
}

.animate {
  -webkit-animation: moveOnTop 1s forwards;
          animation: moveOnTop 1s forwards;
}

@-webkit-keyframes moveOnTop {
  from { -webkit-transform: translateY(35vh); transform: translateY(35vh); }
  to { -webkit-transform: translateY(0); transform: translateY(0); }
}

@keyframes moveOnTop {
  from { -webkit-transform: translateY(35vh); transform: translateY(35vh); }
  to { -webkit-transform: translateY(0); transform: translateY(0); }
}


